import React, { Component, lazy, Suspense } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { getUUID } from "../actions/fakturAction";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { Field, formValueSelector, reduxForm, reset } from "redux-form";
import {
  getBruto,
  getGross,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../actions/LogicAction";
import {
  getStatusTotalan,
  getTableTotalanTimbangan,
} from "../actions/TotalanTimbanganAction";
import HeaderCard from "../HeaderCard";
import Skeleton from "react-loading-skeleton";
import { renderField, renderFieldSelect } from "../renderField";
import { getLokasi } from "../actions/lokasiAction";
import { AxiosMasterPost } from "../Axios";
import { berhasil, warning } from "../Alert";
import { getLokasiPindah } from "../actions/supplierAction";

const FormModalTimbangan = lazy(() =>
  import("./Components/FormModalTimbangan")
);
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);

const { SearchBar } = Search;
class TotalanTimbangan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jenisBarang: "RONGSOK",
      listLokasi: [],
      mulai: false,
      tambahBarang: "col-lg-12 d-none",
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "gross",
          text: "Gross (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "bruto",
          text: "Berat (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar_modal",
          text: "Harga",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getTableTotalanTimbangan());
    this.props.dispatch(getStatusTotalan());
    this.props.dispatch(getLokasi());
    this.props.dispatch(getUUID());
    this.props.dispatch(getLokasiPindah());
    this.props.dispatch(getBruto("totalanTimbangan"));
    this.props.dispatch(getNetto("totalanTimbangan"));
    this.props.dispatch(getPkg("totalanTimbangan"));
    this.props.dispatch(getGross("totalanTimbangan"));
    this.props.dispatch(getRp("totalanTimbangan"));
    this.props.dispatch(getQty("totalanTimbangan"));
    if (localStorage.getItem("totalanTimbangan") !== null) {
      this.setState({
        tambahBarang: "col-lg-12",
      });
    }
  }

  handleSubmit(data) {
    var isi = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT|DEFUALT|DEFAULT|DEFAULT";
    var explode = isi.split("|");
    var simpan = {
      kode_kategori: explode[4],
      kode_jenis: explode[0],
      kadar_cetak: explode[1],
      kadar_modal: explode[3],
      kadar_jual: explode[2],
      qty: 1,
      plastik: 0,
      kulit: 0,
      pkg: parseFloat(data.pkg),
      gross: parseFloat(data.gross),
      bruto: parseFloat(data.gross) - parseFloat(data.pkg),
      netto: parseFloat(data.netto),
    };
    var array = JSON.parse(localStorage.getItem("totalanTimbangan")) || [];
    array.push(simpan);
    localStorage.setItem("totalanTimbangan", JSON.stringify(array));
    AxiosMasterPost(
      `totalan-barang/post-totalan-barang/${this.props.noFaktur}`,
      simpan
    );
    Swal.fire({
      position: "top-right",
      icon: "success",
      text: "Transaksi Berhasil",
      timer: 1500,
      showConfirmButton: false,
    });
    this.props.dispatch(reset("FormModalTimbangan"));
    this.props.dispatch(getTableTotalanTimbangan());
    this.props.dispatch(getStatusTotalan());
  }

  changeInput = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  startTotalan() {
    if (this.props.kode_kelompok === undefined) {
      warning("Mohon isi Kode Kelompok");
      return false;
    } else if (this.props.tanggal === undefined) {
      warning("Mohon Isi Tanggal");
      return false;
    } else if (this.props.lokasiSelected === undefined) {
      warning("Mohon isi Lokasi");
      return false;
    }
    this.setState({
      mulai: true,
    });
    let data = {
      no_totalan: this.props.noFaktur,
      kode_kelompok: this.props.kode_kelompok,
      tanggal: this.props.tanggal,
      kode_lokasi: this.props.lokasiSelected,
    };
    AxiosMasterPost("totalan-barang/start-proses-totalan", data).then(() =>
      this.setState({
        mulai: true,
      })
    );
  }
  batalTotalan() {
    let id_totalan = "";
    if (this.props.status_totalan.no_totalan !== undefined) {
      id_totalan = this.props.status_totalan.no_totalan;
    } else {
      warning("Anda Belum Memulai Totalan");
    }

    AxiosMasterPost(`totalan-barang/close-totalan/${id_totalan}`)
      .then(() => berhasil("Batal Totalan Berhasil"))
      .then(() => localStorage.removeItem("noFaktur"))
      .then(() => localStorage.removeItem("totalanTimbangan"))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => window.location.reload());
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Totalan Timbangan" />
          <div className="card-body">
            <h3>Status Totalan : </h3>
            <button className="btn btn-success mb-3">
              {this.props.status_totalan.no_totalan === undefined
                ? "TOTALAN KOSONG, ANDA BISA LAKUKAN TOTALAN SEKARANG"
                : "TOTALAN SEDANG BERJALAN"}
            </button>
            <br />
            <div className="row">
              <div
                className={
                  this.state.mulai
                    ? "col-lg-12 d-none"
                    : this.props.status_totalan.no_totalan === undefined
                    ? "col-lg-12"
                    : "col-lg-12 d-none"
                }
              >
                <div className="row">
                  <div className="col-lg-4">
                    <Field
                      name="kode_kelompok"
                      component={renderFieldSelect}
                      options={[
                        {
                          value: "PERHIASAN",
                          name: "PERHIASAN",
                        },
                        {
                          value: "CIOK",
                          name: "CIOK",
                        },
                        {
                          value: "RONGSOK",
                          name: "RONGSOK",
                        },
                      ]}
                      type="text"
                      label="Kode Kelompok"
                      placeholder="Masukan "
                    />
                  </div>
                  <div className="col-lg-4">
                    <Field
                      name="tanggal"
                      component={renderField}
                      type="date"
                      label="Tanggal"
                      placeholder="Masukan Tanggal"
                    />
                  </div>
                  <div className="col-lg-4">
                    <Field
                      name="lokasi"
                      component={renderFieldSelect}
                      options={this.props.dataLokasiPindah.map((list) => {
                        let data = {
                          value: list.kode_lokasi,
                          name: list.nama_lokasi,
                        };
                        return data;
                      })}
                      type="text"
                      label="Lokasi"
                      placeholder="Masukan Lokasi"
                    />
                  </div>
                </div>
              </div>
              <div className="col-lg-12 mt-3 mb-3">
                <div className="row">
                  <div className="col-lg-6">
                    <button
                      className="btn btn-danger"
                      onClick={() => this.batalTotalan()}
                    >
                      Batal Totalan
                    </button>
                  </div>
                  <div className="col-lg-6">
                    <div
                      className={
                        this.state.mulai
                          ? "text-right d-none"
                          : this.props.status_totalan.no_totalan === undefined
                          ? "text-right"
                          : "text-right d-none"
                      }
                    >
                      <button
                        className="btn btn-primary"
                        onClick={() => this.startTotalan()}
                      >
                        Mulai Totalan
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div
                className={
                  this.state.mulai
                    ? "col-lg-12"
                    : this.props.status_totalan.no_totalan === undefined
                    ? "col-lg-12 d-none"
                    : "col-lg-12"
                }
              >
                <FormModalTimbangan
                  noFaktur={this.props.noFaktur}
                  jenisBarang={this.props.kode_kelompok}
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
              <div className="col-lg-12 mt-2">
                {this.props.totalanTimbangan ? (
                  <ToolkitProvider
                    keyField="kodeKelompok"
                    data={this.props.totalanTimbangan}
                    columns={this.state.columns}
                    search
                  >
                    {(props) => (
                      <div className="form-group">
                        <div className="text-right mt-5 mb-3">
                          <SearchBar
                            {...props.searchProps}
                            className="form-control"
                            placeholder="Tuliskan Kode / Nama"
                            style={{ margin: 0, top: "50%" }}
                          />
                        </div>
                        <BootstrapTable
                          {...props.baseProps}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          pagination={paginationFactory()}
                        />
                      </div>
                    )}
                  </ToolkitProvider>
                ) : (
                  <BootstrapTable
                    keyField="kodeKelompok"
                    data={[]}
                    columns={this.state.columns}
                    bordered={true}
                    headerClasses="header-table"
                    noDataIndication="Tidak Ada Data"
                  />
                )}
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={5} />}
            >
              <FormModalTimbangan
                noFaktur={this.props.noFaktur}
                jenisBarang={this.props.kode_kelompok}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </Suspense>
          }
          title=""
        />
      </div>
    );
  }
}

TotalanTimbangan = reduxForm({
  form: "TotalanTimbangan",
  enableReinitialize: true,
})(TotalanTimbangan);
const selector = formValueSelector("TotalanTimbangan");
export default connect((state) => {
  const { tanggal, kode_kelompok, lokasi } = selector(
    state,
    "tanggal",
    "kode_kelompok",
    "lokasi"
  );
  return {
    totalanTimbangan: state.provinsi.totalanTimbangan,
    noFaktur: state.provinsi.noFaktur,
    listTotalanTimbangan: state.provinsi.listTotalanTimbangan,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumGross: state.perhitungan.sumGross,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
    sumRp: state.perhitungan.sumRp,
    onSend: state.provinsi.onSend,
    status_totalan: state.provinsi.status_totalan,
    lokasi: state.provinsi.lokasi,
    tanggal: tanggal,
    kode_kelompok: kode_kelompok,
    lokasiSelected: lokasi,
    dataLokasiPindah: state.provinsi.dataLokasiPindah,
    initialValues: {
      kode_kelompok: state.provinsi.status_totalan.kode_kelompok,
      tanggal: state.provinsi.status_totalan.tanggal,
    },
  };
})(TotalanTimbangan);
